'use client';
import React, { useState } from 'react'
import TaskList from './components/TaskList';
import TaskModel from './components/TaskModel';
import TaskView from './components/TaskView';
import TaskForm from './components/TaskForm';
import { useAuth } from './lib/AuthContext';

export default function Homepage() {
  const [tasks, setTasks] = useState([
    { id: 1, title: 'Task 1', status: "Pending", description: "Description for Task 1" },
    { id: 2, title: 'Task 2', status: "Completed", description: "Description for Task 2" },
  ]);
   const {user,logout} = useAuth();
   console.log("User:",user);
  const [isAdding, setIsAdding] = useState(false);
  const [isEditing, setIsEditing] = useState(null);
  const [selectedTask, setSelectedTask] = useState(null);
  const [formData, setFormData] = useState({ title: '', status: '', description: '' });

  const handleAddTask = (e) => {
    e.preventDefault();
    const newTask = { id: Date.now(), ...formData };
    setTasks([...tasks, newTask]);
    setFormData({ title: '', status: 'Pending', description: '' });
    setIsAdding(false);
  };

  const handleDelete = (id) => {
    if (confirm("Are you sure you want to delete this task?")) {
      setTasks(tasks.filter(task => task.id !== id));
      setSelectedTask(null);
    }
  };

  const handleUpdateTask = (e) => {
    e.preventDefault();
    setTasks(tasks.map(t => t.id === isEditing.id ? { ...isEditing, ...formData } : t));
    setIsEditing(null);
    setFormData({ title: '', status: '', description: '' });
  };

  return (
    <div className='min-h-screen bg-gray-50 p-8'>
      <div className='max-w-2xl mx-auto'>
        <div className='flex justify-between items-center mb-8'>
          <h1 className='text-3xl font-extrabold text-gray-900'>My Tasks</h1>
          <button
            className='px-4 py-2 bg-blue-600 text-white rounded-md'
            onClick={() => setIsAdding(true)}
          >
            Add Task
          </button>
          {
            user && (
              <div className='flex items-center gap-4'>
              <img src={user.photoURL || 'https://via.placeholder.com/40'}
                alt='User'
                className='w-8 h-8 rounded-full border'
              />
               <button className='px-4 py-2 bg-red-600 text-white rounded-md' onClick={logout}>Logout</button>'
              </div>
            )
          }
        </div>


        <TaskList
          tasks={tasks}
          onView={setSelectedTask}
          onEdit={(task) => { setIsEditing(task); setFormData(task); }}
          onDelete={handleDelete}
        />

        {selectedTask && (
          <TaskModel onClose={() => setSelectedTask(null)}>
            <TaskView task={selectedTask} />
          </TaskModel>
        )}

        {/* ✅ Only ONE Add Task Modal */}
        {isAdding && (
          <TaskModel onClose={() => setIsAdding(false)}>
            <h2 className='text-xl font-bold mb-4'>Add New Task</h2>
            <TaskForm
              onSubmit={handleAddTask}
              setFormData={setFormData}
              formData={formData}
              buttonLabel="Add Task"
              onCancel={() => setIsAdding(false)}
            />
          </TaskModel>
        )}

        {/* ✅ Edit Task Modal */}
        {isEditing && (
          <TaskModel onClose={() => setIsEditing(null)}>
            <h2 className='text-xl font-bold mb-4'>Edit Task</h2>
            <TaskForm
              onSubmit={handleUpdateTask}
              setFormData={setFormData}
              formData={formData}
              buttonLabel="Update Task"
              onCancel={() => setIsEditing(null)}
            />
          </TaskModel>
        )}
      </div>
    </div>
  );
}
